.cell {
    height: 4vw;
    width: 4vw;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    border: 1px solid lightskyblue;
}

.cell > .letter {
    margin: auto;
    font-size: 18px;
    text-align: center;
}

.cell-input {
    border: none;
    width: 3.8vw;
    height: 3.8vw;
    text-align: center;
}

input::-webkit-input-placeholder {
    text-align: center;
}

input:-ms-input-placeholder {
    text-align: center;
}

input::-ms-input-placeholder {
    text-align: center;
}

input::placeholder {
    text-align: center;
}

.cell-input:hover {
    background-color: lightskyblue;
}

.shouldColor {
    background-color: yellow;
}


